import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './style/index.less'
import vueUtil from './js/vueUtil'
import deviceQueries from './plugins/device-queries'
import bus from './js/bus'
import SvgIcon from '@/components/svgIcon.vue'

import '@/js/animate/jquery'
import '@/js/animate/vegas.js'
import '@/js/animate/vegas.css'
import '@/js/animate/animate.min.css'
// import '@/js/animate/wow.js'

// 
Vue.component('svg-icon', SvgIcon)
// import './js/flexible.js'

// 
import VuePhotoZoomPro from 'vue-photo-zoom-pro'

Vue.use(VuePhotoZoomPro)

Vue.use(vueUtil)
// 
Vue.config.productionTip = false
// 
Vue.use(deviceQueries, {
  phone: 'max-width: 567px',
  tablet: 'min-width: 568px',
  mobile: 'max-width: 1024px',
  laptop: 'min-width: 1025px',
  desktop: 'min-width: 1280px',
  monitor: 'min-width: 1448px'
})
new Vue({
  created() {
    // 
    bus.$on('logout', () => {
      this.$cookies.removeItem('jwt')
      this.$router.replace('/login')
    })
  },
  router,
  store,
  render: h => h(App)
}).$mount('#app')
