<template>
  <div v-if="showAside" class="aside">
    <div class="icon-wrapper">
      <svg-icon icon-class="business_icon" class="icon"></svg-icon>
      <div class="contact">
        <div>
          <div class="title"></div>
          <p @click="handleContact">{{phone}}</p>
        </div>
      </div>
    </div>
    <div class="icon-wrapper">
      <svg-icon icon-class="wechat_icon" class="icon"></svg-icon>
      <img :src="qrcode" alt />
    </div>
    <div class="icon-wrapper">
      <svg-icon icon-class="tiktok_icon" class="icon"></svg-icon>
      <div class="tiktok-wrapper">
        <img class="tiktok" :src="douyin1" alt />
      </div>
    </div>

    <div class="icon-wrapper" @click="handleGoStore">
      <svg-icon icon-class="yunzhanting_icon" class="icon"></svg-icon>
    </div>

    <div class="icon-wrapper" @click="handleGoTop">
      <svg-icon icon-class="top_icon" class="icon"></svg-icon>
    </div>
  </div>
</template>

<script>
import '@/assets/icons/wechat_icon.svg'
import '@/assets/icons/top_icon.svg'
import '@/assets/icons/tiktok_icon.svg'
import '@/assets/icons/business_icon.svg'
import '@/assets/icons/yunzhanting_icon.svg'
import qrcode from '@/assets/qrcode.png'
import douyin from '@/assets/douyin.png'
import douyin1 from '@/assets/douyin_1.png'

import { mapGetters } from 'vuex'
export default {
  name: 'webAside',
  props: {
    msg: String
  },
  data () {
    return {
      qrcode,
      douyin,
      douyin1,
      phone: '0755-25505266'
    }
  },
  computed: {
    ...mapGetters([
      'showAside'
    ])
  },
  methods: {
    handleGoTop () {
      $('html, body').animate({
        scrollTop: 0
      }, 600)
    },
    handleContact () {
      this.$fn.callPhone(this.phone)
    },
    handleGoStore () {
      window.open('http://www.swiky.com.cn:2577/', '_blank')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@import '~@/style/tools/_mediaqueries';
@keyframes iconFlad {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.aside {
  position: fixed;
  right: 24px;
  bottom: 153px;
  .phone({bottom: 60px; right: 15px;});
  animation: fadeIn 0.3s;
  z-index: 100;
  .icon-wrapper {
    position: relative;
    margin-bottom: 25px;
    &:last-child {
      margin-bottom: 0;
    }
    &:hover {
      & > img,
      .contact,
      .tiktok-wrapper {
        // animation: iconFlad .3s;
        visibility: visible;
        // opacity: 1;
        // animation: fadeIn .3s;
      }
    }
    & > img,
    .tiktok-wrapper,
    .contact {
      position: absolute;
      visibility: hidden;
      // opacity: 0;
      top: 0;
      left: -104px;
      width: 100px;
      height: 100px;
      // &.tiktok {
      //   width: 200px;
      //   height: auto;
      //   left: -202px;
      // }
      // transition: opacity .3s;
    }
    .contact {
      background-color: #fff;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);
      .title {
        font-size: 14px;
        color: #dab866;
      }
      p {
        margin-top: 5px;
        cursor: pointer;
      }
    }
    .tiktok-wrapper {
      // visibility: visible;
      padding: 3px;
      background-color: #fff;
      width: 100px;
      height: auto;
      img {
        width: 94px;
      }
    }
  }
