<template>
  <transition :name="transitionName" mode="out-in">
    <div class="slide-web" flex="dir:top" :class="{wth100: isMobile || wth100, 'from-parent': fromParent, 'slide-bottom': slideBottom}" :style="slideStyleInner" v-if="isShow" ref="slide">
      <template v-if="need">
        <h1 class="slide-web_header ">
          <a-icon @click.stop="cancel"  type="left"></a-icon>
          {{ title }}
        </h1>
      </template>
      <slot ></slot>
    </div>
  </transition>
</template>

<script>
import { Icon } from 'ant-design-vue'
export default {
  name: 'slide-left-web',
  components: {
    [Icon.name]: Icon
  },
  created () {
    this.isShow = this.value
    this.slideStyleInner = this.slideStyle
  },
  mounted () {
  },
  data () {
    return {
      isShow: false,
      slideStyleInner: '',
      isMobile: /Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)
    }
  },
  methods: {
    cancel () {
      this.isShow = false
      this.$emit('input', this.isShow)
      this.$emit('back')
    }
  },
  props: {
    value: Boolean,
    slideStyle: String,
    title: String,
    zIndex: Number,
    cancelStyle: String,
    wth100: Boolean,
    backIcon: {
      type: Boolean,
      default: false
    },
    need: {
      type: Boolean,
      default: true
    },
    transitionName: {
      type: String,
      default: 'slideLeft'
    },
    fromParent: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    slideBottom () {
      return this.transitionName === 'slideBottom'
    }
  },
  watch: {
    value (val) {
      this.isShow = val
    },
    isShow (val) {
      if (val) {
        this.$nextTick(() => {
          if (window.parent && window.parent.frameElement && window.parent.frameElement.tagName === 'IFRAME') {
            window.parent.document.body.appendChild(this.$refs.slide)
          } else {
            // 
            if (this.fromParent) {
              const layout = document.querySelector('.ant-layout-content')
              layout && layout.appendChild(this.$refs.slide)
            } else {
              document.body.appendChild(this.$refs.slide)
            }
          }
          let slideLength = document.querySelectorAll('.slideLeft').length
          // 
          if (slideLength > 1) {
            if (this.zIndex) {
              slideLength += this.zIndex
            }
            this.slideStyleInner += `;z-index:${slideLength + 1};`
          }
        })
      }
    }
  }
}
</script>

<style lang="less" rel="stylesheet/less">
  .wth100 {
    width: 100%;
  }
  .slide-web {
    position: fixed;
    top: 24px;
    bottom: 24px;
    right: 0;
    left: 16px;
    z-index: 9;
    background: #fff;
    .slide-web_header{
      padding:24px 24px 16px 24px;
      font-size:20px;
    }
    &.from-parent {
      position: absolute;
      /*&:after {*/
        /*content: '';*/
        /*position: fixed;*/
        /*top: 0;*/
        /*bottom: 0;*/
        /*right: 0;*/
        /*left: 0;*/
        /*background-color: transparent;*/
        /*z-index: -1;*/
      /*}*/
    }
    &.slide-bottom {
      width: auto;
      left: 24px;
      right: 24px;
      top: 49px;
      bottom: 0;
    }
    .cancel {
      position: absolute;
      z-index: 12;
      right: 10px;
      top: 10px;
      font-size: 18px;
      cursor: pointer;
    }
  }
  // 
  .slideLeft-enter-active,
  .slideLeft-leave-active {
    opacity: 1;
    transition: all .3s ease;
  }
  .slideLeft-enter,
  .slideLeft-leave-to {
    opacity: 0;
    transform: translate3d(30%, 0, 0);
  }
  // 
  .slideBottom-enter-active,
  .slideBottom-leave-active {
    opacity: 1;
    transition: all .3s ease;
  }
  .slideBottom-enter,
  .slideBottom-leave-to {
    opacity: 0;
    transform: translate3d(0, 30%, 0);
  }
</style>
