/* eslint-disable */
import Vue from 'vue'
import VueRouter from 'vue-router'

const Home = () =>
  import( /* webpackChunkName: "web" */ '../views/web/home/index')
const Boss = () =>
  import( /* webpackChunkName: "boss" */ '../views/boss/index.vue')
const Company = () => import( /* webpackChunkName: "boss" */ '../views/boss/company/index.vue')
const Banner = () => import( /* webpackChunkName: "boss" */ '../views/boss/company/banner.vue')
const Category = () => import( /* webpackChunkName: "boss" */ '../views/boss/goods/category.vue')
const Series = () => import( /* webpackChunkName: "boss" */ '../views/boss/goods/series.vue')
const Good = () => import( /* webpackChunkName: "boss" */ '../views/boss/goods/good.vue')
const RecommendSeries = () => import( /* webpackChunkName: "boss" */ '../views/boss/company/recommendSeries')
const ArticleGroup = () => import( /* webpackChunkName: "boss" */ '../views/boss/article/group.vue')
const ArticleList = () => import( /* webpackChunkName: "boss" */ '../views/boss/article/index.vue')
const Cooperation = () => import( /* webpackChunkName: "boss" */ '../views/boss/cooperation/group.vue')
const CooperationList = () => import( /* webpackChunkName: "boss" */ '../views/boss/cooperation/index.vue')
const NewsGroups = () => import( /* webpackChunkName: "boss" */ '../views/boss/news/group')
const NewsList = () => import( /* webpackChunkName: "boss" */ '../views/boss/news/index')






// '==================================='
// 
const Login = () => import( /* webpackChunkName: "web" */ '../views/web/login/index.vue')
// 
const RCategory = () => import( /* webpackChunkName: "web" */ '../views/web/goods/category')
const RCategoryDetail = () => import( /* webpackChunkName: "web" */ '../views/web/goods/categoryDetail')
const WebNews = () => import( /* webpackChunkName: "web" */ '../views/web/news/index.vue')
const NewsDetail = () => import( /* webpackChunkName: "web" */ '../views/web/newsDetail/index.vue')
const Test = () => import( /* webpackChunkName: "web" */ '../views/web/test/index.vue')
// 
const Corporate = () => import( /* webpackChunkName: "web" */ '../views/web/corporate/index.vue')
Vue.use(VueRouter)

// 
const routes = [{
    path: '/',
    component: Banner
  },
  {
    path: 'company',
    component: Company
  },
  {
    path: 'articlelist',
    component: ArticleList
  },
  {
    path: 'articlegroups',
    component: ArticleGroup
  },
  {
    path: 'newslist',
    component: NewsList
  },
  {
    path: 'newsgroups',
    component: NewsGroups
  },
  {
    path: 'category',
    component: Category
  },
  {
    path: 'series',
    component: Series
  },
  {
    path: 'good',
    component: Good
  },
  {
    path: 'banner',
    component: Banner
  },
  {
    path: 'recommend',
    component: RecommendSeries
  },
  {
    path: 'cooperation',
    component: Cooperation
  },
  {
    path: 'cooperationlist',
    component: CooperationList
  }
]

//  router-view
const bossRoutes = {
  path: '/boss',
  component: Boss,
  children: routes
}
// 
const homeRoutes = [{
    path: '/',
    component: Home,
    meta: {
      web: true, // truenavBar
      keepAlive: true
    }
  },
  {
    path: '/category/:id',
    component: RCategory,
    meta: {
      web: true
    }
  },
  {
    path: '/series/:category/:index',
    component: RCategoryDetail,
    meta: {
      web: true
    }
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  { // ()  
    path: '/webNews/:id',
    component: WebNews,
    meta: {
      web: true // truenavBar
    }
  },
  { // 
    path: '/newsDetail/:id',
    component: NewsDetail,
    meta: {
      web: true // truenavBar
    }
  },
  {
    path: '/test',
    component: Test,
    meta: {
      web: true // truenavBar
    }
  },
  {
    path: '/corporate',
    component: Corporate,
    meta: {
      web: true // truenavBar
    }
  }
]
const complexRouter = [bossRoutes, ...homeRoutes]

console.log(complexRouter)
const router = new VueRouter({
  routes: complexRouter,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    }
    return {
      x: 0,
      y: 0
    }
  }
})

export default router
