
export function offsetTop (element) {
  var top;
  top = element.offsetTop;
  while (element = element.offsetParent) {
    top += element.offsetTop;
  }
  return top
}
export function offsetLeft (element) {
  var left;
  left = element.offsetLeft;
  while (element = element.offsetParent) {
    left += element.offsetLeft;
  }
  return left
}
// 
export function isVisible (box, configOffset) {

  var bottom, offset, top, viewBottom, viewTop
  var element = window.document.documentElement
  offset = configOffset || 0
  viewTop = window.pageYOffset
  viewBottom = viewTop + element.clientHeight - offset
  top = offsetTop(box)
  bottom = top + box.clientHeight
  return top <= viewBottom && bottom >= viewTop
}

// 
export function horizontalVisible (box, configOffset) {

  var right, offset, left, viewRight, viewLeft
  var element = window.document.documentElement
  offset = configOffset || 0
  viewLeft = window.pageXOffset
  viewRight = viewLeft + element.clientWidth - offset
  left = offsetLeft(box)
  right = left + box.clientWidth
  return left <= viewRight && right >= viewLeft
}

// Scroll
if (!requestAnimationFrame) {
  requestAnimationFrame = function (fn) {
    setTimeout(fn, 17)
  }
}

Math.easeout = function (A, B, rate, callback) {
  if (A == B || typeof A != 'number') {
    return;
  }
  B = B || 0;
  rate = rate || 2;

  var step = function () {
    A = A + (B - A) / rate;

    if (A < 1) {
      callback(B, true);
      return;
    }
    callback(A, false);
    requestAnimationFrame(step);
  };
  step();
};
export function moveTop (doc, cb) {

  Math.easeout(doc.scrollTop, 0, 14, function (value, stopped) {
    doc.scrollTop = value;
    cb(stopped)
  });
}
