import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    nav: [],
    showAside: true,
    firstProduct: {} // 
  },
  mutations: {
    setNav: (state, data) => {
      state.nav = data || 0
    },
    setAside: (state, data) => {
      state.showAside = data || 0
    },
    setFirstProduct: (state, data) => {
      state.firstProduct = data || 0
    },
  },
  getters: {
    nav: state => state.nav,
    showAside: state => state.showAside,
    firstProduct: state => state.firstProduct,
  },
  actions: {
  },
  modules: {
  }
})
