import { QUERY_URL } from '../config'
import axios from 'axios'
import querystring from 'querystring'
import docCookies from '../docCookies'
import bus from '@bus'
import { err } from '../fn'
const timeOut = 1000 * 15

export function query (
  functionName,
  data = {},
  cb = () => {},
  serviceName,
  httpType = 'post',
  then
) {
  const params = {
    functionName,
    serviceName,
    httpType
  }
  let baseUrl = ''

  if (data.noLoading) {
    delete data.noLoading
  } else if (!then) {
    bus.$emit('showLoading')
    // $.showLoading()
  }
  baseUrl = `${QUERY_URL}/${serviceName}/${functionName}`

  if (data._queryUrl) {
    baseUrl = data._queryUrl
    delete data._queryUrl
  }
  if (httpType === 'get') {
    baseUrl += `?${querystring.stringify(data)}`
  }
  const option = {
    method: httpType,
    url: baseUrl,
    timeout: timeOut,
    headers: {
      'cache-control': 'no-cache',
      'Content-Type': 'application/json',
      'x-jwt-token': docCookies.getItem('jwt')
    },
    data: Object.assign(params, data)
  }
  if (data.setHeaders) {
    option.headers = Object.assign(option.headers, data.setHeaders)
  }
  if (then) {
    return axios(option)
  }
  // console.log(option)
  return axios(option)
    .then(res => {
      // cb('ajax-chenggong')
      bus.$emit('hideLoading')
      if (res.status === 200 && res.data.result !== 'FALSE') {
        if (res.data.result !== 'TRUE') {
          err(res.data.msg, 4000)
          return
        }
        try {
          cb(res.data)
        } catch (e) {
          console.log(e)
        }
      } else {
        if (res.data.msg === 'PERMISSION_DENIED') {
          bus.$emit('logout')
          return
        }
        err(res.data.msg, 4000)
      }
    })
    .catch(res => {
      bus.$emit('hideLoading')
      // cb('ajax--' + res)
      try {
        cb(res)
      } catch (e) {
        console.log(e)
      }
    })
}
