<template>
  <div id="app" @click="handleApp($event)">
    <WebTabBar ref="webTabBar" class="top" v-if="$route.meta.web" />
    <div class="content" :class="{isWeb: $route.meta.web}">
      <keep-alive>
        <router-view v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
      <router-view v-if="!$route.meta.keepAlive && $route.meta.web" :key="$route.fullPath"></router-view>
      <router-view v-if="!$route.meta.keepAlive && !$route.meta.web"></router-view>
    </div>
    <web-footer class="bottom" v-if="$route.meta.web" />
    <web-aside v-if="$route.meta.web" />
    <a-modal
      :width="$device.phone ? '100vw' : '60vw'"
      :visible="previewVisible"
      :footer="null"
      @cancel="handleCancel"
    >
      <img alt="example" style="width: 100%" :src="previewImage" />
    </a-modal>
  </div>
</template>

<script>
import WebTabBar from '@/components/webTabBar'
import WebFooter from '@/components/webFooter'
import WebAside from '@/components/webAside'
import { Modal } from 'ant-design-vue'
// import VueDisbounce from 'vue-disbounce'
import bus from '@bus'
export default {
  name: 'Home',
  components: {
    WebTabBar,
    WebFooter,
    WebAside,
    [Modal.name]: Modal
  },
  data() {
    return {
      previewVisible: false,
      previewImage: ''
    }
  },
  created() {
    bus.$on('showImgModal', url => {
      console.log(url)
      this.handleShowImg(url)
    })
  },
  methods: {
    handleShowImg(url) {
      this.previewVisible = true
      this.previewImage = url
    },
    handleCancel() {
      this.previewVisible = false
    },
    handleApp(event) {
      if (this.$refs.webTabBar) {
        if (event.target.innerText) {
          this.$refs.webTabBar.hidePhoneCode()
          // if (event.target.innerText.trim() !== '' && event.target.innerText.trim() !== '') {
          //   this.$refs.webTabBar.hidePhoneCode()
          // }
        } else {
          this.$refs.webTabBar.hidePhoneCode()
        }
      }
    }
  }
}
</script>

<style scoped lang="less">
#app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  -webkit-overflow-scrolling: touch;
  .content {
    &.isWeb {
      margin-top: 80px;
    }
    flex: 1;
  }
}
</style>
