import { query } from './query'

export function addGroup (data, cb) {
  query('addGroup', data, cb, 'article')
}
export function listGroup (data, cb) {
  query('listGroup', data, cb, 'article', 'get')
}
export function delGroup (data, cb) {
  query('delGroup', data, cb, 'article')
}
