import { query } from './query'
// 
export function homeData (data, cb) {
  query('indexData', data, cb, 'home', 'get')
}

// 
export function getNewsDetail (data, cb) {
  query('detail', data, cb, 'article', 'get')
}

// 
export function getNewsList (data, cb) {
  query('listArticle', data, cb, 'article', 'get')
}

// 
export function getCompanyIntro (data, cb) {
  query('listCorporateImage', data, cb, 'admin', 'get')
}