import {
  message
} from 'ant-design-vue'
import imageConversion from 'image-conversion-es5'
export function formatDate(date, format) {
  if (Object.prototype.toString.call(date) !== '[object Date]') {
    date = new Date(date.replace(/-/g, '/'))
  }
  var paddNum = function (num) {
    num += ''
    return num.replace(/^(\d)$/, '0$1')
  }
  // 
  var cfg = {
    yyyy: date.getFullYear(), //  : 4
    yy: date.getFullYear().toString().substring(2), //  : 2
    M: date.getMonth() + 1, //  : 10
    MM: paddNum(date.getMonth() + 1), //  : 10
    d: date.getDate(), //  : 10
    dd: paddNum(date.getDate()), //  : 10
    h: date.getHours(), // 
    hh: paddNum(date.getHours()), // 
    mm: paddNum(date.getMinutes()), // 
    ss: paddNum(date.getSeconds()) // 
  }
  format || (format = 'yyyy-MM-dd hh:mm:ss')
  return format.replace(/([a-z])(\1)*/ig, function (m) {
    return cfg[m]
  })
}

export function getCss(o, key) {
  return o.currentStyle ? o.currentStyle[key] : document.defaultView.getComputedStyle(o, false)[key]
}

export function filterDate(created) {
  if (isNaN(+created)) {
    created = created.replace(/-/g, '/')
  } else {
    created = +created
  }
  const date = new Date(created)

  const now = new Date()

  // 
  const toDay = new Date().setHours(0, 0, 0, 0)

  const day = 24 * 60 * 60 * 1000

  if (date.getDate() === now.getDate()) {
    return formatDate(date, 'hh:mm')
  }

  // 
  if (+date <= +toDay && date >= (+toDay - day)) {
    return ' ' + formatDate(date, 'hh:mm')
  }

  // 
  const year = new Date().getFullYear()
  const firstDayInYear = new Date(year, 0, 1)

  if (date > firstDayInYear) {
    return formatDate(date, 'MM-dd hh:mm')
  } else {
    return formatDate(date, 'YYYY-MM-dd hh:mm')
  }
}

export function dataURLtoBlob(dataurl, toBlob) {
  var arr = dataurl.split(',')
  // base64
  var _arr = arr[1].substring(0, arr[1].length - 2)
  var mime = arr[0].match(/:(.*?);/)[1]
  var bstr = atob(_arr)
  var n = bstr.length
  var u8arr = new Uint8Array(n)
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n)
  }

  const blob = new Blob([u8arr], {
    type: mime
  })

  if (toBlob) {
    return blob
  }
  return URL.createObjectURL(blob)
}

export function err(word) {
  message.error(word)
}
export function success(word) {
  message.success(word)
}



// 
var class2type = {}

var toString = class2type.toString

export function isBoolean(target) {
  return target === true || target === false || toString.call(target) === '[object Boolean]'
}

//
export function isObject(target) {
  return typeof (target) === 'object'
}

//
export function allkeys(obj) {
  if (!isObject(obj)) {
    return []
  }
  let keys = [],
    key
  for (key in obj) {
    keys.push(key)
  }
  return keys
}


//
export function isPlainObject(target) {
  return target && isObject(target) && Object.getPrototypeOf(target) === Object.prototype
}
// 
export const isArray = Array.isArray || function (target) {
  return toString.call(target) === '[object Array]'
}
// 
export function extend( /*isdeep,*/ obj) {
  let len = arguments.length,
    idx = 1,
    isdeep = false
  if (len < 2 || obj === null) {
    return obj
  }
  if (isBoolean(arguments[0])) {
    isdeep = arguments[0]
    obj = arguments[1]
    idx++
  }
  let source, keys, proplen, i, key, iscopyarr, clone
  for (; idx < len; idx++) { //
    source = arguments[idx] //
    keys = allkeys(source)
    proplen = keys.length
    for (i = 0; i < proplen; i++) {
      key = keys[i]
      if (source[key] === obj) { //
        continue
      }
      if (isdeep && (isPlainObject(source[key]) || (iscopyarr = isArray(source[key])))) {
        if (iscopyarr) {
          iscopyarr = false
          clone = obj[key] && isArray(obj[key]) ? obj[key] : []
        } else {
          clone = obj[key] && isPlainObject(obj[key]) ? obj[key] : {}
        }
        obj[key] = extend(isdeep, clone, source[key])
      } else if (void 0 !== source[key]) {
        obj[key] = source[key]
      }
    }
  }
  return obj
}

